  --color-text--3: #d3c7c3;
  --color-on-primary: #fff;
  --color-input-borders: #ddd;
  --color-grey--3: #e7e5e4;
  --gradient: linear-gradient(
    to right bottom,
    var(--color-grad-1),
    var(--color-grad-2)
