@import "https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap";
:root {
  --color-primary: #e62a15;
  --color-grad-1: #f5af19;
  --color-grad-2: #f12711;
  --color-primary-variant: #f12711;
  --color-wrapper: #ff922b;
  --color-background: #fff;
  --color-grey--1: #f9f5f3;
  --color-grey--2: #f2efee;
  --color-text--1: #615551;
  --color-text--2: #918581;
  --color-text--3: #d3c7c3;
  --color-on-primary: #fff;
  --color-input-borders: #ddd;
  --color-grey--3: #e7e5e4;
  --gradient: linear-gradient(to right bottom, var(--color-grad-1), var(--color-grad-2));
  --wrapper-gradient: linear-gradient(to right bottom, var(--color-wrapper), var(--color-grad-2));
}

[data-theme="dark"] {
  --color-primary: #ffc078;
  --color-grad-1: #303030;
  --color-grad-2: #303030;
  --color-primary-variant: #ff922b;
  --color-wrapper: #121212;
  --color-background: #303030;
  --color-grey--1: #424242;
  --color-grey--2: #383838;
  --color-text--1: #ececec;
  --color-text--2: #bec5c7;
  --color-text--3: #989898;
  --color-on-primary: #000;
  --color-input-borders: #121212;
  --color-grey--3: #e7e5e4;
  --gradient: linear-gradient(to right bottom, var(--color-primary-variant), var(--color-primary-variant));
  --wrapper-gradient: linear-gradient(to right bottom, var(--color-wrapper), var(--color-wrapper));
}

* {
  margin: 0;
  padding: 0;
  transition: background-color .3s;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 41.25em) {
  html {
    font-size: 43.75%;
  }
}

@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  color: var(--color-text--1);
  background-image: var(--wrapper-gradient);
  min-height: calc(100vh - 8vw);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  line-height: 1.6;
}

.container-home {
  max-width: 120rem;
  min-height: 115rem;
  background-color: var(--color-background);
  border-radius: 9px;
  grid-template: "head head" 10rem
                 "list recipe" minmax(100rem, auto)
                 / 1fr 2fr;
  margin: 4vw auto;
  display: grid;
  overflow: hidden;
  box-shadow: 0 2rem 6rem .5rem #0003;
}

@media only screen and (max-width: 75em) {
  .container-home {
    max-width: 100%;
    border-radius: 0;
    margin: 0;
  }
}

@media only screen and (max-width: 41.25em) {
  .container-home {
    grid-template: "head" 10rem
                   "list"
                   "recipe"
                   / 1fr;
  }
}

.container-pages {
  max-width: 120rem;
  min-height: 100rem;
  background-color: var(--color-background);
  border-radius: 9px;
  grid-template: "head" 10rem
                 "main" minmax(100rem, auto)
                 / 1fr;
  margin: 4vw auto;
  display: grid;
  overflow: hidden;
  box-shadow: 0 2rem 6rem .5rem #0003;
}

@media only screen and (max-width: 75em) {
  .container-pages {
    max-width: 100%;
    border-radius: 0;
    margin: 0;
  }
}

@media only screen and (max-width: 41.25em) {
  .container-pages {
    grid-template-rows: 10rem minmax(130rem, auto);
  }

  .container-pages__planner {
    grid-template-rows: 10rem auto;
  }
}

.section {
  text-align: center;
  margin-bottom: 6rem;
  padding: 5rem 8rem 0;
}

.section__title {
  margin-bottom: 2rem;
}

.section__title:after, .section__title:before {
  content: "";
  width: 35%;
  height: 1px;
  background-color: var(--color-text--3);
  display: inline-block;
  transform: translateY(1.5rem);
}

.section__title:before {
  float: left;
}

.section__title:after {
  float: right;
}

@media only screen and (max-width: 56.25em) {
  .section__title:after, .section__title:before {
    width: 0%;
  }
}

@media only screen and (max-width: 41.25em) {
  .section__shopping-list {
    margin-top: 3rem;
  }
}

.section__list {
  justify-items: center;
  display: grid;
}

.section__planner {
  flex-direction: column;
  place-content: center space-between;
  display: flex;
}

@media only screen and (max-width: 75em) {
  .section__planner {
    padding: 5rem 5rem 0;
  }
}

@media only screen and (max-width: 41.25em) {
  .section__planner {
    height: -moz-fit-content;
    height: -moz-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    justify-content: flex-start;
    margin-top: 8rem;
    padding: 0;
  }

  .section__recipe {
    padding: 0;
  }
}

.section__features {
  padding: 0;
}

.btn, .btn--small, .btn--small:link, .btn--small:visited {
  background-image: var(--gradient);
  text-transform: uppercase;
  color: var(--color-on-primary);
  cursor: pointer;
  border: none;
  border-radius: 10rem;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.btn:hover, .btn--small:hover {
  transform: scale(1.05);
}

.btn:focus, .btn--small:focus {
  outline: none;
}

.btn > :first-child, .btn--small > :first-child {
  margin-right: 1rem;
}

.btn {
  padding: 1.5rem 4rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.btn svg {
  height: 2.25rem;
  width: 2.25rem;
  fill: currentColor;
}

.btn--small, .btn--small:link, .btn--small:visited {
  padding: 1.25rem 2.25rem;
  font-size: 1.4rem;
  font-weight: 700;
  text-decoration: none;
}

.btn--small svg, .btn--small:link svg, .btn--small:visited svg {
  height: 1.75rem;
  width: 1.75rem;
  fill: currentColor;
}

.btn--inline {
  color: var(--color-primary);
  background-color: var(--color-grey--1);
  cursor: pointer;
  border: none;
  border-radius: 10rem;
  align-items: center;
  padding: .8rem 1.2rem;
  font-size: 1.3rem;
  font-weight: 600;
  transition: all .2s;
  display: flex;
}

.btn--inline svg {
  height: 1.6rem;
  width: 1.6rem;
  fill: currentColor;
  margin: 0 .2rem;
}

.btn--inline span {
  margin: 0 .4rem;
}

.btn--inline:hover {
  color: var(--color-primary-variant);
  background-color: var(--color-grey--2);
}

.btn--inline:focus {
  outline: none;
}

.btn--week {
  background-color: var(--color-grey--1);
  color: var(--color-text--1);
  cursor: pointer;
  border: none;
  border-radius: 10rem;
  align-items: center;
  padding: 1.2rem 1.6rem;
  font-size: 1.3rem;
  font-weight: 600;
  transition: all .2s;
  display: flex;
}

.btn--week svg {
  height: 1.6rem;
  width: 1.6rem;
  fill: currentColor;
  margin: 0 .2rem;
}

.btn--week span {
  text-transform: uppercase;
}

.btn--week:hover {
  color: var(--color-primary-variant);
  background-color: var(--color-grey--2);
}

.btn--week:focus {
  outline: none;
}

.btn--round {
  background-image: var(--gradient);
  cursor: pointer;
  height: 4.5rem;
  width: 4.5rem;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.btn--round:hover {
  transform: scale(1.07);
}

.btn--round:focus {
  outline: none;
}

.btn--round svg {
  height: 2.5rem;
  width: 2.5rem;
  fill: var(--color-on-primary);
}

.btn--tiny {
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  background: none;
  border: none;
}

.btn--tiny svg {
  height: 100%;
  width: 100%;
  fill: var(--color-primary);
  transition: all .3s;
}

.btn--tiny:focus {
  outline: none;
}

.btn--tiny:hover svg {
  fill: var(--color-primary-variant);
  transform: translateY(-1px);
}

.btn--tiny:active svg {
  fill: var(--color-primary-variant);
  transform: translateY(0);
}

.btn--tiny:not(:last-child) {
  margin-right: .3rem;
}

.btn--white {
  width: -moz-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-image: none;
  background-color: var(--color-grey--2);
  color: var(--color-primary);
  align-self: center;
}

.btn--white:not(:last-child) {
  margin-bottom: 2rem;
}

.btn--list {
  color: var(--color-primary);
  background-color: var(--color-grey--2);
  cursor: pointer;
  border: none;
  border-radius: 1rem;
  align-items: center;
  padding: .8rem 1rem;
  font-size: 2rem;
  font-weight: 600;
  transition: all .2s;
  display: flex;
}

.btn--list:not(:last-child) {
  margin-right: 1rem;
}

.btn--list svg {
  height: 2.4rem;
  width: 2.4rem;
  fill: currentColor;
  margin: 0 .2rem;
  transform: translateX(2px);
}

.btn--list .list__icon--add {
  transform: translate(4px, 4px);
}

.btn--list span {
  margin: 0 3rem;
}

.btn--list:hover {
  color: var(--color-primary-variant);
  filter: brightness(.95);
}

.btn--list:focus {
  outline: none;
}

.heading--2 {
  color: var(--color-primary);
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 2rem;
  font-weight: 700;
}

.heading--3 {
  color: var(--color-text--1);
  text-transform: uppercase;
  text-align: center;
  margin: 1.5rem 0;
  font-size: 1.6rem;
  font-weight: 700;
}

.link:link, .link:visited {
  color: var(--color-text--2);
}

.spinner {
  text-align: center;
  margin: 5rem auto;
}

.spinner svg {
  height: 6rem;
  width: 6rem;
  fill: var(--color-primary);
  animation: 2s linear infinite rotate;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.message, .error {
  max-width: 40rem;
  margin: 0 auto;
  padding: 5rem 4rem;
  display: flex;
}

.message svg, .error svg {
  height: 3rem;
  width: 3rem;
  fill: var(--color-primary);
  transform: translateY(-.3rem);
}

.message p, .error p {
  color: var(--color-text--1);
  margin-left: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 1.5;
}

.message a:link, .message a:visited, .error a:link, .error a:visited {
  color: var(--color-primary);
  text-decoration: none;
}

.message a:hover, .error a:hover {
  text-decoration: underline;
}

.header {
  background-color: var(--color-grey--1);
  grid-area: head;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  align-items: center;
  margin-left: 2.5rem;
  display: flex;
}

@media only screen and (max-width: 56.25em) {
  .header__logo {
    margin-left: 2.5rem;
    margin-right: 1.5rem;
  }
}

@media only screen and (max-width: 41.25em) {
  .header__logo {
    margin-left: 2.5rem;
    margin-right: 1rem;
  }
}

.header__logo-img {
  height: 5rem;
  margin-right: 1rem;
}

@media only screen and (max-width: 75em) {
  .header__logo-img--home {
    margin-right: 0;
  }
}

.header__logo-name {
  font-family: Akaya Kanadaka, serif;
  font-size: 3.4rem;
  transform: translateY(-3px);
}

@media only screen and (max-width: 75em) {
  .header__logo-name--home {
    display: none;
  }
}

.search {
  background-color: var(--color-grey--2);
  border-radius: 10rem;
  align-items: center;
  margin-right: 1.5rem;
  padding-left: 3rem;
  transition: all .3s;
  display: flex;
}

.search:focus-within {
  transform: translateY(-2px);
}

.search__field {
  color: inherit;
  width: 25rem;
  background: none;
  border: none;
  font-family: inherit;
  font-size: 1.7rem;
}

.search__field:focus {
  outline: none;
}

.search__field::placeholder {
  color: var(--color-text--3);
}

@media only screen and (max-width: 56.25em) {
  .search__field {
    width: auto;
    max-width: 14rem;
  }

  .search__field::placeholder {
    color: var(--color-grey--2);
  }
}

.search__btn {
  padding: 1.5rem 3rem;
  font-family: inherit;
  font-weight: 700;
}

@media only screen and (max-width: 75em) {
  .search__btn {
    padding: 1.5rem 2.5rem;
  }

  .search__btn span {
    display: none;
  }

  .search__btn svg {
    margin-right: 0 !important;
  }
}

@media only screen and (max-width: 41.25em) {
  .search__btn {
    padding: 1.5rem 3rem;
  }

  .search__btn span {
    display: initial;
  }

  .search__btn svg {
    margin-right: 1rem !important;
  }
}

@media only screen and (max-width: 28.12em) {
  .search__btn {
    padding: 1.5rem 2.5rem;
  }

  .search__btn span {
    display: none;
  }

  .search__btn svg {
    margin-right: 0 !important;
  }
}

.search__icon--header {
  margin-right: 0;
}

.page-options {
  align-self: stretch;
}

@media only screen and (max-width: 41.25em) {
  .page-options {
    background-color: var(--color-grey--1);
    z-index: 100;
    border-radius: 3rem;
    padding: 1rem .5rem;
    position: absolute;
    top: 11rem;
    right: 2rem;
  }
}

.page-options__list {
  height: 100%;
  list-style: none;
  display: flex;
}

.page-options__item {
  position: relative;
}

.page-options__btn {
  height: 100%;
  color: inherit;
  text-transform: uppercase;
  cursor: pointer;
  background: none;
  border: none;
  align-items: center;
  padding: 0 1rem;
  font-family: inherit;
  font-size: 1.4rem;
  font-weight: 700;
  transition: all .3s;
  display: flex;
}

.page-options__btn--home {
  text-decoration: none;
}

.page-options__btn svg {
  height: 2.4rem;
  width: 2.4rem;
  fill: var(--color-primary);
  margin-right: .7rem;
  transform: translateY(-1px);
}

.page-options__btn:focus {
  outline: none;
}

.page-options__btn:hover {
  background-color: var(--color-grey--2);
}

.page-options-end {
  width: 100%;
  justify-content: flex-end;
  margin-right: .5rem;
  display: flex;
}

@media only screen and (max-width: 41.25em) {
  .page-options-end {
    width: -moz-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: 0;
    display: block;
  }
}

.bookmarks {
  z-index: 10;
  width: 40rem;
  background-color: var(--color-grey--1);
  visibility: hidden;
  opacity: 0;
  padding: 1rem 0;
  transition: all .5s .2s;
  position: absolute;
  right: -2.5rem;
  box-shadow: 1rem 1rem 4rem #dddddd1a;
}

@media only screen and (max-width: 41.25em) {
  .bookmarks {
    right: 0;
  }
}

.bookmarks__list {
  list-style: none;
}

.bookmarks__field {
  cursor: pointer;
  height: 100%;
  align-items: center;
  padding: 0 4rem;
  transition: all .3s;
  display: flex;
}

.bookmarks__field:hover {
  background-color: var(--color-grey--2);
}

.bookmarks:hover, .page-options__btn--bookmarks:hover + .bookmarks {
  visibility: visible;
  opacity: 1;
}

.bookmarks .preview__link:link, .bookmarks .preview__link:visited {
  border: none;
}

.bookmarks .preview__link:link:hover, .bookmarks .preview__link:visited:hover {
  background-color: var(--color-grey--2);
}

.slider {
  width: 8.5rem;
  height: 4.2rem;
  background-color: var(--color-grey--2);
  border-radius: 2rem;
  justify-content: space-between;
  align-items: center;
  margin: 0 1rem;
  padding: 1rem 1.2rem;
  display: flex;
  position: relative;
}

.slider > :not(:last-child) {
  margin-right: 1rem;
}

.slider svg {
  width: 2.5rem;
  height: 2.5rem;
  fill: var(--color-text--3);
}

.slider__circle {
  content: "";
  width: 2.8rem;
  height: 2.8rem;
  z-index: 100;
  background-image: var(--gradient);
  border-radius: 50%;
  transition: all .2s;
  display: block;
  position: absolute;
  left: 1rem;
}

@media only screen and (max-width: 28.12em) {
  .slider {
    background-color: var(--color-grey--1);
    z-index: 100;
    position: absolute;
    top: 11rem;
    left: 2rem;
  }
}

.navigation {
  min-width: 8rem;
  height: 100%;
  margin-left: 1rem;
  margin-right: 3rem;
  position: relative;
}

@media only screen and (max-width: 41.25em) {
  .navigation {
    margin-right: 2rem;
  }
}

.navigation__checkbox {
  display: none;
}

.navigation__button {
  background-image: var(--gradient);
  height: 5.3rem;
  width: 8rem;
  z-index: 2000;
  cursor: pointer;
  border-radius: 10rem;
  transition: all .4s;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1rem 2rem #00000026;
}

.navigation__background {
  background-image: var(--wrapper-gradient);
  opacity: .8;
  height: 5rem;
  width: 5rem;
  z-index: 1000;
  border-radius: 50%;
  transition: all .8s cubic-bezier(.86, 0, .07, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.navigation__nav {
  height: 100vh;
  z-index: 1500;
  visibility: hidden;
  opacity: 0;
  width: 0;
  transition: all .8s cubic-bezier(.48, -.36, .42, 1.39);
  position: fixed;
  top: 0;
  left: 0;
}

.navigation__list {
  text-align: center;
  width: 100%;
  list-style: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navigation__item {
  margin: .5rem;
}

.navigation__link:link, .navigation__link:visited {
  color: #fff;
  text-transform: uppercase;
  background-image: linear-gradient(120deg, transparent 0%, transparent 50%, var(--color-grey--3) 50%);
  clip-path: polygon(10% 0, 100% 0, 90% 100%, 0 100%);
  background-size: 220%;
  padding: 1.3rem 5rem;
  font-size: 3.2rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .6s;
  display: inline-block;
}

.navigation__link:hover, .navigation__link:active {
  color: #000;
  background-position: 100%;
}

.navigation__icon {
  position: relative;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navigation__icon, .navigation__icon:before, .navigation__icon:after {
  background-color: var(--color-grey--1);
  height: 3px;
  width: 3rem;
  border-radius: 1rem;
  transition: all .3s;
  display: inline-block;
}

.navigation__icon:after, .navigation__icon:before {
  content: "";
  position: absolute;
  left: 0;
}

.navigation__icon:before {
  top: -1rem;
}

.navigation__icon:after {
  top: 1rem;
}

.navigation__icon:hover .navigation__icon:before {
  top: -1.2rem;
}

.navigation__icon:hover .navigation__icon:after {
  top: 1.2rem;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: #0000;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon:before {
  background-color: var(--color-primary);
  top: 0;
  transform: rotate(135deg);
}

.navigation__checkbox:checked + .navigation__button .navigation__icon:after {
  background-color: var(--color-primary);
  top: 0;
  transform: rotate(-135deg);
}

.navigation__checkbox:checked ~ .navigation__background {
  transform: translate(-50%, -50%)scale(150);
}

.navigation__checkbox:checked ~ .navigation__button {
  background-image: linear-gradient(var(--color-grey--1), var(--color-grey--2));
}

.navigation__checkbox:checked ~ .navigation__nav {
  opacity: 1;
  visibility: initial;
  width: 100%;
}

.preview__link:link, .preview__link:visited {
  border-right: 1px solid var(--color-background);
  align-items: center;
  padding: 1.5rem 3.25rem;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.preview__link:hover {
  background-color: var(--color-grey--1);
  transform: translateY(-2px);
}

.preview__link--active {
  background-color: var(--color-grey--1);
}

.preview__fig {
  height: 5.8rem;
  backface-visibility: hidden;
  border-radius: 50%;
  flex: 0 0 5.8rem;
  margin-right: 2rem;
  position: relative;
  overflow: hidden;
}

.preview__fig:before {
  content: "";
  height: 100%;
  width: 100%;
  background-image: linear-gradient(to right bottom, var(--color-grad-1), var(--color-grad-2));
  opacity: .4;
  display: block;
